import React, {useEffect, useState} from 'react';
import { Table } from 'semantic-ui-react';
import styles from './read.css'
import axios from 'axios';

function Read() {
    const [APIData, setAPIData] = useState([]);
    useEffect(() => {
            axios.get('https://63489b3f0b382d796c74614d.mockapi.io/dataNotas')
                .then((response) => {
                    setAPIData(response.data);
                })
    }, [])
    return (
        <div>
            <h1>SERVIÇOS REALISADOS</h1>
            <Table singleLine id='tabela'>
                <Table.Header id='cabeca'>
                    <Table.Row>

                        <Table.HeaderCell>Data</Table.HeaderCell>
                        <Table.HeaderCell>Cliente</Table.HeaderCell>
                        <Table.HeaderCell>Equipamento</Table.HeaderCell>
                        <Table.HeaderCell>SERVIÇOS</Table.HeaderCell>
                        
                        
                        
                    </Table.Row>
                </Table.Header>
                <Table.Body>
                {APIData.map((data) => {
                    return (
                    <Table.Row id='tab'>
                        <Table.Cell>{data.Data}</Table.Cell>
                        <Table.Cell>{data.Cliente}</Table.Cell>
                        <Table.Cell>{data.EquipamentoMarca + data.Equipamento}</Table.Cell>
                        <Table.Cell>{data.Serviso}</Table.Cell>
                        </Table.Row>
                )})}
                </Table.Body>
            </Table> 
        </div>
    )
}
export default Read; 