import React from 'react'
import { useState } from 'react';
import { Button, Form, List } from 'semantic-ui-react'
import axios from 'axios';



 function Create() {
    const [Cliente, setCliente] = useState('');
    const [EquipamentoMarca, setMarcas ] = useState('');
    const [Equipamento, setModelo] = useState('');
    const [Serviso, OS] = useState('');
    const [Data, setData] = useState('');

   // const modeloCaseApi = ('https://63489b3f0b382d796c74614d.mockapi.io/marcaCase')
    //const modeloU = document.getElementById('modelosUsuario')

    //window.addEventListener('load', async () =>{
        //const request = await fetch(modeloCaseApi);
        //const response = await request.json()

        //console.log(response)
        //const options = document.createElement('optgroup')
        //options.setAttribute('label', "modeloU")
        //response.forEach(function(modelo){
          //  options.innerHTML += "<option>"+modelo.Modelo+"</option>"
            
        //})
        //modeloU.append(options)

    //})
    

    const postData = () => {
        axios.post('https://63489b3f0b382d796c74614d.mockapi.io/dataNotas', {
            Cliente,
            EquipamentoMarca,
            Equipamento,
            Serviso,
            Data,
           
            
        })
    
        
    }
    
    return (
        
        <div>
            
            <h1 id='titulop'>Cadastro Serviço</h1>
            <Form className="create-form">
                <Form.Field>
                    <input type='Date' placeholder='Data' onChange={(e) => setData(e.target.value)}></input>
                </Form.Field>
                <br></br>
                <Form.Field>
                    <input placeholder='CLIENTE' onChange={(e) => setCliente(e.target.value)}/>
                </Form.Field>
                <br></br>
                <Form.Field>
                <select defaultValue='Marcas' onChange={(e) => setMarcas(e.target.value)}>
                        <option disabled>Marcas</option>
                        <option value='CAT- '>CAT</option>
                        <option value='CASE-'>CASE</option>
                        <option value='NW-Holy-'>NEW HOLLAND</option>
                        <option value='MF-'>MASSEY FERGUNSON</option>
                        <option value='BOB CAT-'>BOB CAT</option>
                        <option value='Empilhadeira-'>EMPLIHADEIRA</option>
                        <option value='VACOL-'>VACOL</option>
                        <option value='AGRALE-'>AGRALE</option>
                    </select>
                    
                </Form.Field>  
                <br></br>  
                <Form.Field>
                     <input placeholder='Modelo' onChange={(e) => setModelo(e.target.value)}/>
                    
                </Form.Field>
                <br></br>
                <Form.Field>
                    <input type='textarea' placeholder='SERVISOS' onChange={(e) => OS(e.target.value)}></input>
                </Form.Field>
                   
                <Button onClick={postData} type='submit'>Salvar</Button>
            </Form>
        </div>
    )
    
}

export default Create;